"use client"

import { useEffect, useState } from "react"
import { useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"

interface CSVRow {
  id: string
  data: Record<string, string>
  scraped: boolean
}

export default function CSVViewPage({ params }: { params: { filename: string } }) {
  const router = useRouter()
  const [rows, setRows] = useState<CSVRow[]>([])
  const [currentIndex, setCurrentIndex] = useState(0)
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)

  useEffect(() => {
    const loadCSVData = async () => {
      try {
        // For demo purposes, we'll create some sample data
        // In a real app, you'd fetch this from an API endpoint that reads the CSV file
        const sampleData = [
          { id: '1', data: { column1: 'Value 1', column2: 'Value 2' }, scraped: false },
          { id: '2', data: { column1: 'Value 3', column2: 'Value 4' }, scraped: false },
          { id: '3', data: { column1: 'Value 5', column2: 'Value 6' }, scraped: false },
        ]
        setRows(sampleData)
        setLoading(false)
      } catch (err) {
        setError('Failed to load CSV data')
        setLoading(false)
      }
    }

    loadCSVData()
  }, [params.filename])

  const handleScrape = async (rowId: string) => {
    try {
      // Mark the current row as scraped
      setRows(prevRows =>
        prevRows.map(row =>
          row.id === rowId ? { ...row, scraped: true } : row
        )
      )

      // Move to the next row
      setCurrentIndex(prev => {
        const nextIndex = prev + 1
        if (nextIndex < rows.length) {
          return nextIndex
        }
        // If we've reached the end, stay at the last index
        return prev
      })

      // Here you would typically navigate to your scraping URL
      // For now, we'll just log it
      console.log(`Scraping row ${rowId} from file ${params.filename}`)
      
      // You can uncomment this when you have the actual URL
      // router.push('your-scraping-url-here')
    } catch (err) {
      setError('Failed to process scraping')
    }
  }

  if (loading) {
    return (
      <div className="container mx-auto p-4">
        <Card>
          <CardContent className="p-6">
            Loading...
          </CardContent>
        </Card>
      </div>
    )
  }

  const currentRow = rows[currentIndex]
  const hasMoreRows = currentIndex < rows.length - 1

  return (
    <div className="container mx-auto p-4">
      <Card>
        <CardHeader>
          <CardTitle className="text-2xl font-bold">
            Viewing {params.filename}
          </CardTitle>
        </CardHeader>
        <CardContent>
          {error && (
            <div className="text-red-500 mb-4">{error}</div>
          )}
          
          {currentRow && !currentRow.scraped && (
            <div className="space-y-4">
              <Table>
                <TableHeader>
                  <TableRow>
                    <TableHead>Field</TableHead>
                    <TableHead>Value</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {Object.entries(currentRow.data).map(([key, value]) => (
                    <TableRow key={key}>
                      <TableCell className="font-medium">{key}</TableCell>
                      <TableCell>{value}</TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>

              <div className="flex justify-between">
                <Button
                  variant="outline"
                  onClick={() => router.push('/files')}
                >
                  Back to Files
                </Button>
                <Button
                  onClick={() => handleScrape(currentRow.id)}
                >
                  Scrape
                </Button>
              </div>
            </div>
          )}

          {(!currentRow || currentRow.scraped) && !hasMoreRows && (
            <div className="text-center space-y-4">
              <p>All rows have been processed!</p>
              <Button
                variant="outline"
                onClick={() => router.push('/files')}
              >
                Back to Files
              </Button>
            </div>
          )}
        </CardContent>
      </Card>
    </div>
  )
}
